@font-face {
  font-family: menuItemFont;
  src: url(./TCCEB.TTF);
}
@keyframes pulse {
  to {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0);
  }
}

// canvas
#tsparticles {
  height: 100vh;
  width: 100%;
  position: absolute;
}

.videoArea {
  width: 30%;
  border-radius: 30px;
  margin-bottom: 30px;
}

// header.js
#myHeader {
  width: 100%;
  height: 10vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: absolute;
  background: #00000000;
  transition: all 0.6s ease;
  &.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    background: #00000054;
    border-bottom: 0;
    transition: all 0.6s ease;
    &.white {
      background: #00000054;
      box-shadow: 0 4px 10px 0 rgba(10, 10, 10, 0.8);
    }
  }
  .mainside {
    width: 90%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: space-between;
    .menuItem {
      display: flex;
      div {
        margin-right: 30px;
        font-family: menuItemFont;
        color: white;
        font-size: 20px;
        cursor: pointer;
      }
    }
    .menuBtnArea {
      display: flex;
      .btn-main {
        color: white;
        text-align: center;
        border-radius: 30px;
        font-weight: 800;
        padding: 15px 40px;
        box-shadow: 0 0 0 0 white;
        background: linear-gradient(50deg, #6f583f98, #6d737183);
        animation: pulse 2s infinite 3s cubic-bezier(0.25, 0, 0, 1);
        cursor: pointer;
        span {
          font-size: 15px;
          display: flex;
          align-items: center;
          img {
            width: 25px;
          }
        }
      }
      .space {
        width: 30px;
      }
      .btn-icons {
        display: flex;
        div {
          cursor: pointer;
          position: relative;
          border: 3px solid transparent;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
          background: #252f26;
          margin: 0 5px 0 5px;
        }
        div::after {
          content: "";
          position: absolute;
          top: -5px;
          bottom: -5px;
          left: -5px;
          right: -5px;
          z-index: -1;
          border-radius: 50%;
          background: linear-gradient(to right, #b4b5b3, #363932);
        }
      }
    }
  }
}

